/* -------------------------------------------------------------------------- 
	#. Variables
  -------------------------------------------------------------------------- */
:root {
  --adfy_wishlist_text_color: #111111;
  --adfy_wishlist_link_color: blue;
  --adfy_wishlist_link_hover_color: #111111;
  --adfy_wishlist_button_color: #111111;
  --adfy_wishlist_button_hover_color: #111111;
  --adfy_wishlist_button_text_color: #111111;
  --adfy_wishlist_button_text_hover_color: #111111;
  --adfy_wishlist_button_border_color: #111111;
  --adfy_wishlist_button_border_hover_color: #111111;
  --adfy_wishlist_button_border_radius: 0;
  --adfy_wishlist_button_border_width: 0;
  --adfy_wishlist_button_border_style: solid;
}

/* -------------------------------------------------------------------------- 
	#. Mixins
  -------------------------------------------------------------------------- */
/* -------------------------------------------------------------------------- 
	#. Wishlist button
  -------------------------------------------------------------------------- */
.adfy-wishlist-btn {
  font-weight: normal;
  cursor: pointer;
  transition: all 0.5s ease;
}
.adfy-wishlist-btn .show-label .adfy-wishlist-icon {
  margin-right: 5px;
}

.adfy-wishlist-clear-button-style {
  padding: 0;
  margin: 0;
  color: #333333;
  cursor: pointer;
  border-color: none;
  background-color: transparent;
}

.adfy-wishlist-clear-button-style:hover {
  color: grey;
  border-color: none;
  background-color: transparent;
}

.addonify-add-to-wishlist-btn button[disabled] {
  cursor: not-allowed;
  opacity: 0.6 !important;
}

.addonify-add-to-wishlist-btn .before-add-to-cart {
  margin-bottom: 1em;
}

.addonify-add-to-wishlist-btn .after-add-to-cart {
  margin-top: 1em;
}

.addonify-overlay-buttons {
  position: relative;
}

.addonify-overlay-buttons .addonify-overlay-btn-wrapper {
  position: absolute;
  width: 100%;
  top: 0;
  left: 0;
  opacity: 0;
  flex-direction: column;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgba(0, 0, 0, 0.8);
  transition: all 0.5s ease;
}

.addonify-overlay-buttons .addonify-overlay-btn-wrapper .addonify-overlay-btn {
  opacity: 1;
  box-shadow: 0px 0px 65px rgba(0, 0, 0, 0.1);
}

/* -------------------------------------------------------------------------- 
	#. Tippy styles
  -------------------------------------------------------------------------- */
.tippy-box[data-animation=scale][data-placement^=top] {
  transform-origin: bottom;
}

.tippy-box[data-animation=scale][data-placement^=bottom] {
  transform-origin: top;
}

.tippy-box[data-animation=scale][data-placement^=left] {
  transform-origin: right;
}

.tippy-box[data-animation=scale][data-placement^=right] {
  transform-origin: left;
}

.tippy-box[data-animation=scale][data-state=hidden] {
  transform: scale(0.5);
  opacity: 0;
}

.tippy-box {
  font-size: 15px;
  line-height: 1.3;
  font-weight: normal;
  padding: 2px 10px 3px 10px;
  border-radius: 2px;
}

/* -------------------------------------------------------------------------- 
	#. Wishlist modal
  -------------------------------------------------------------------------- */
#addonify-wishlist-modal-wrapper {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 999999;
  background: rgba(0, 0, 0, 0.8);
}

#addonify-wishlist-modal-wrapper .addonify-wishlist-modal-body {
  width: 350px;
  min-height: 350px;
  text-align: center;
  padding: 30px;
  margin: calc((100vh - 300px) / 2) auto 0;
  border-radius: 5px;
  box-sizing: border-box;
  background-color: white;
  box-shadow: 0px 0px 65px rgba(0, 0, 0, 0.1);
}

#addonify-wishlist-modal-wrapper .adfy-wishlist-icon {
  display: inline-block;
  font-size: 42px;
  color: red;
  animation: AddonifyWishlistPulse 0.8s infinite;
}

#addonify-wishlist-modal-wrapper #addonify-wishlist-modal-response {
  margin: 20px 0;
}

#addonify-wishlist-modal-wrapper .addonify-wishlist-modal-btns button {
  width: 100%;
  margin-top: 10px;
}

#addonify-wishlist-modal-wrapper.loading .addonify-wishlist-modal-body {
  background: white url(../../images/loading.svg) no-repeat 50%;
}

#addonify-wishlist-modal-wrapper.loading .addonify-wishlist-modal-body * {
  display: none !important;
}

#addonify-wishlist-modal-wrapper.require-login form {
  margin: 0;
}

body.addonify-wishlist-modal-is-open {
  overflow: hidden;
}

body.addonify-wishlist-modal-is-open #addonify-wishlist-modal-wrapper {
  display: block;
}

/* -------------------------------------------------------------------------- 
